


























































































import { Component, Vue } from 'vue-property-decorator';
import jQuery from 'jQuery';
import axios from 'axios';
// tslint:disable-next-line:no-var-requires
const https = require('https');
import Modal from '@/components/Modal.vue';
import { setTimeout } from 'timers';

@Component({
  components: {
    Modal,
  },
})
export default class ContactContent extends Vue {
  private lastName: string = '';
  private lastNameValid: boolean = false;
  private firstName: string = '';
  private firstNameValid: boolean = false;
  private email: string = '';
  private emailValid: boolean = false;
  private phone: string = '';
  private message: string = '';
  private messageValid: boolean = false;
  private selectedType: string = '';
  private typeValid: boolean = false;
  private selectedState: string = '';
  private stateValid: boolean = false;
  private states: string[] = new Array<string>(
    '北海道',
    '青森県',
    '岩手県',
    '宮城県',
    '秋田県',
    '山形県',
    '福島県',
    '茨城県',
    '栃木県',
    '群馬県',
    '埼玉県',
    '千葉県',
    '東京都',
    '神奈川県',
    '新潟県',
    '富山県',
    '石川県',
    '福井県',
    '山梨県',
    '長野県',
    '岐阜県',
    '静岡県',
    '愛知県',
    '三重県',
    '滋賀県',
    '京都府',
    '大阪府',
    '兵庫県',
    '奈良県',
    '和歌山県',
    '鳥取県',
    '島根県',
    '岡山県',
    '広島県',
    '山口県',
    '徳島県',
    '香川県',
    '愛媛県',
    '高知県',
    '福岡県',
    '佐賀県',
    '長崎県',
    '熊本県',
    '大分県',
    '宮崎県',
    '鹿児島県',
    '沖縄県',
    'Foreign Country',
  );

  get validateLastName(): boolean {
    if (this.lastName.length > 0) {
      this.lastNameValid = true;
      return true;
    }
    this.lastNameValid = false;
    return false;
  }

  get validateFirstName(): boolean {
    if (this.firstName.length > 0) {
      this.firstNameValid = true;
      return true;
    }
    this.firstNameValid = false;
    return false;
  }

  get validateType(): boolean {
    if (this.selectedType.length > 0) {
      this.typeValid = true;
      return true;
    }
    this.typeValid = false;
    return false;
  }

  get validateState(): boolean {
    if (this.selectedState.length > 0) {
      this.typeValid = true;
      return true;
    }
    this.typeValid = false;
    return false;
  }

  get validateEmail(): boolean {
    const regexp = /^[A-Za-z0-9]{1}[A-Za-z0-9_.-]*@{1}[A-Za-z0-9_.-]{1,}\.[A-Za-z0-9]{1,}$/;

    if (regexp.test(this.email)) {
      this.emailValid = true;
      return true;
    }
    this.emailValid = false;
    return false;
  }

  get validateMessage(): boolean {
    if (this.message.length > 0) {
      this.messageValid = true;
      return true;
    }
    this.messageValid = false;
    return false;
  }

  private mounted() {
    this.fadein();
    $('.content').scroll(() => {
      this.fadein();
    });
  }

  private created() {
    setTimeout(() => {
      $('.content').css('top', '60px');
    }, 1000);
  }

  private fadein(): void {
    const offset = - 60;
    const effectPos = $(window).height()! - offset;
    $('.column').each( function() {
      const thisPos = $(this).offset()!.top;
      if ( effectPos > thisPos ) {
        $(this).css({
            'opacity': 1,
            'transform': 'translate(0px, 0px)',
            '-ms-filter': 'blur(0px)',
            'filter': 'blur(0px)',
        });
      } else {
        $(this).css({
            'opacity': 0,
            'transition-delay': '0s',
        });
      }
    });
  }

  private onSubmit(): void {
    if (!this.lastNameValid || !this.firstNameValid || !this.emailValid
     || !this.messageValid || !this.selectedType || !this.selectedState) {
      alert('無効な項目があります');
      return;
    }

    ($('.ui.basic.modal')as any).modal({
      closable: false,
    })
    .modal('show');

    const data = {
      name: this.lastName + ' ' + this.firstName,
      organization: this.selectedType,
      state: this.selectedState,
      email: this.email,
      phone: this.phone,
      message: this.message,
    };
    const header = {
      Authorization: `Bearer ${this.$store.state.idToken}`,
    };

    axios.post('https://flask.site:443/contact', data, {
      headers: header,
    })
    .then((res) => {
      ($('.ui.basic.modal')as any).modal('hide');
      if (!res.data.result) {
        alert('送信に失敗しました');
        return;
      }
      this.lastName = '';
      this.firstName = '';
      this.selectedType = '';
      this.selectedState = '';
      this.email = '';
      this.phone = '';
      this.message = '';
      alert('送信が完了しました');
    })
    .catch((err) => {
      console.log(err);
      ($('.ui.basic.modal')as any).modal('hide');
      alert('送信に失敗しました');
    });
  }
}
